import { Hidden } from '@material-ui/core'
import React, { useState } from 'react'
import styled from 'styled-components'

export default function Friend(props){

    const [background, setBackground] = useState("#ffffff")

    const setStyle = (background) => {
        setBackground(background)
    }

    const Container = styled.div`
        width: 17.5vw;
        height: 3.75vw;
        border-bottom: 1px solid whitesmoke;
        &:hover{
            cursor: pointer;
        }
    `
    const StreakBox = styled.div` 
        float: left;
        width: 2vw;
        height: 1vw;
        margin-top: 2.5vw;
        display: ${props.streak == '0' ? 'none' : 'default'};
    `
    const Streak = styled.img` 
        width: 0.75vw;
        float: left;
        padding: 0.25vw;
    `
    const StreakNum = styled.h4`
        margin-top: 0.5vw;
        margin-left: -0.1vw;
        font-size: 0.5vw;
        float: left;
    `
    const Photo = styled.img` 
        border-radius: 50%;
        width: 2.5vw;
        margin: 0.5vw;
        float: left;
        margin-left: ${props.streak == '0' ? '1vw' : '-1vw'};
    `
    const Name = styled.h1` 
        font-size: 0.65vw;
        padding-top: 0.75vw;
        opacity: ${props.icon == "./assets/icons/sentmessage.png" ? '50%' : '100%'};
    `
    const Time = styled.h2`
        padding-top: 0.25vw;
        font-size: 0.5vw;
        color: ${props.icon == "./assets/icons/newsnap.png" ? '#ED245E' : '#0397DF'};
        opacity: ${props.icon == "./assets/icons/sentmessage.png" ? '50%' : '100%'};
    `
    const Icon = styled.img` 
        width: 0.75vw;
        margin: 1vw;
        margin-right: 0;
        float: right;
    `
    const Num = styled.h3` 
        font-size: 0.75vw;
        padding: 0.5vw;
        float: right;
        margin-left: -0.5vw;
        color: ${props.time == "just now" ? '#ED245E' : '#0397DF'};
    `

    return(
        <Container>
            <StreakBox>
                <Streak src="/assets/icons/fire.jpg"/>
                <StreakNum>{props.streak}</StreakNum>
            </StreakBox>
            <Photo src={props.img}/>
            <div style={{float: "left"}}>
                <Name>{props.name}</Name>
                <Time>{props.time}</Time>
            </div>
            <Num>{props.num}</Num>
            <Icon src={props.icon}/>

            
        </Container>
    )
}