import React from 'react'

export default function FriendInfo(props){
    return(
        <div className="friendinfo">
            <img src={props.img} className="friendinfophoto"/>
            <h1 style={{textAlign: "center", fontSize: "2vw", paddingTop:"0.5vw"}}>{props.name}</h1>
            <div className="locationbutton">
                <img src="/assets/icons/location.png" style={{width: "1vw", float: "left"}}/>
                <h1>Send My Location</h1>
            </div>
            <div className="locationbutton">
            <img />
                <img src="/assets/icons/location.png" style={{width: "1vw", float: "left"}}/>
                <h1>See TechLead on Maps</h1>
            </div>
            <div className="savedmedia">    
                <h1>Saved Media</h1>
            </div>
        </div>
    )
}