import React from 'react'
import styled from 'styled-components'

import Friend from './components/Friend'
import FriendInfo from './components/FriendInfo'
import ChatBubble from './components/ChatBubble'

export default function Chat(){
    window.onkeydown=function(event){
        if(event.keyCode==13){
            console.log("enter")
            document.getElementById("textarea").value = "";
            <ChatBubble message={document.getElementById("textarea").value}/>
        }
    }
return(
<div>
    <div className="messages">
        <div className="messagesheader">
            <img className="profilephoto" src="/assets/photos/mahad.jpg"></img>
            <h1>Chat</h1>
            <img className="icon" src="/assets/icons/addfriend.png" />
            <img className="icon" src="/assets/icons/settings.png" />
            <img className="icon" src="/assets/icons/search.png" />
        </div>
        <Friend 
            name="TechLead"
            streak="0"
            time="2 minutes ago"
            img="./assets/photos/techlead.jpg" 
            icon="./assets/icons/newmessage.png"
            num="1 "
        />
        <Friend 
            name="Mohammad Baber Zia"
            streak="3"
            time="just now"
            img="./assets/photos/baber.jpg" 
            icon="./assets/icons/newsnap.png"
            num="3 "
        />
        <Friend 
            name="benzhang_3"
            streak="44"
            time="less than a minute ago"
            img="./assets/photos/ben.jpg" 
            icon="./assets/icons/newmessage.png"
            num="1 "
        />
        <Friend
            streak="0"
            name="ronathan.A"
            time="sent 1h ago"
            img="./assets/photos/ronathan.jpg" 
            icon="./assets/icons/sentmessage.png"
        />

        <div className="navbar">
            <img className="navicon" style={{marginLeft: "3.5vw"}} src="/assets/icons/messages.png"/>
            <img className="navicon" src="/assets/icons/location.png" />
            <img className="navicon" src="/assets/icons/camera.png" />
            <img className="navicon" src="/assets/icons/settings2.png" />
        </div>
    </div>
    <div className="chat">
        <div className="friendtitle">
            <img src="/assets/photos/techlead.jpg" style={{float: "left", borderRadius: "50%", width: "2vw", margin: "0.5vw"}}/>
            <h1 style={{float: "left", marginTop: "1vw", fontSize: "1.25vw"}}>TechLead</h1>
            <img src="/assets/icons/settings.png" className="icon" style={{float: "right", width: "1.5vw", margin: "0.5vw"}}/>
        </div>

        <div className="typearea">
            <img className="typeareaicon" src="/assets/icons/add.png"/>
            <img className="typeareaicon" src="/assets/icons/photo.png"/>
            <textarea id="textarea" placeholder="Aa"></textarea>

        </div>
    </div>

    <FriendInfo
        img="/assets/photos/techlead.jpg"
        name="TechLead"
    />
</div>
)
}